import React, { useRef, useState } from "react";
import "./App.css";
import Fade from "react-reveal/Fade";
import Zoom from "react-reveal/Zoom";
import LightSpeed from "react-reveal/LightSpeed";

import firebase from "firebase/app";
import "firebase/firestore";
import "firebase/auth";
import "firebase/analytics";
import "firebase/storage";

import { useAuthState } from "react-firebase-hooks/auth";
import { useCollectionData } from "react-firebase-hooks/firestore";

firebase.initializeApp({
  apiKey: "AIzaSyDI2Z_jsnX8XurTrTnL_CONCP5CH-aX72g",
  authDomain: "paul-s-chatapp.firebaseapp.com",
  projectId: "paul-s-chatapp",
  storageBucket: "paul-s-chatapp.appspot.com",
  messagingSenderId: "91537495329",
  appId: "1:91537495329:web:2e101410a844957aa5da60",
});

const auth = firebase.auth();
const firestore = firebase.firestore();

function App() {
  const [user] = useAuthState(auth);

  return (
    <div className="App">
      <Zoom>
        <header>
          <h2 style={{ alignItems: "center" }}> Paul's ChatRoom</h2>
          <SignOut />
        </header>
      </Zoom>
      <section>{user ? <ChatRoom /> : <SignIn />}</section>
    </div>
  );
}

function SignIn() {
  const signInWithGoogle = () => {
    const provider = new firebase.auth.GoogleAuthProvider();
    auth.signInWithPopup(provider);
  };

  return (
    <span>
      <Fade right>
        <img
          className="ChatAppLogo"
          height="300"
          width="600"
          src="https://lh3.googleusercontent.com/pw/ACtC-3eda_4zuOMS8Y8izQKvnBYHnVdSNLlTJKvg2zwm0wt7T8Yji18ue1B4yckiHrpXS7ESpMfSuOHGFzNdgkEnHAltRmiRsLTRXPbI4maEnneWw1Hbr6hZZTAVBTO3ML_zrifpspLyDrtkolnf67VsYIHr=w438-h240-no?authuser=0"
          alt="ChatRoomImage"
          onClick={() => alert("Welcoeme to Paul's ChatRoom")}
        />
      </Fade>
      <LightSpeed left>
        <h2
          style={{
            fontFamily: "cursive",
            fontWeight: "bolder",
            color: "black",
            backgroundColor: "whitesmoke",
          }}
        >
          Welcome To Paul's ChatRoom
        </h2>
      </LightSpeed>
      <Fade bottom>
        <button className="sign-in" onClick={signInWithGoogle}>
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/2/2d/Google-favicon-2015.png"
            alt="google.png"
            height="30"
            width="30"
          />
          Sign in with Google
        </button>
      </Fade>
    </span>
  );
}

function SignOut() {
  return (
    auth.currentUser && (
      <button
        style={{
          backgroundColor: "yellow",
          color: "black",
          fontFamily: "monospace",
        }}
        className="sign-out"
        onClick={() => auth.signOut()}
      >
        Sign Out
      </button>
    )
  );
}

function ChatRoom() {
  const dummy = useRef();
  const messagesRef = firestore.collection("messages");
  const query = messagesRef.orderBy("createdAt").limit(25);

  const [messages] = useCollectionData(query, { idField: "id" });

  const [formValue, setFormValue] = useState("");

  const sendMessage = async (e) => {
    e.preventDefault();

    const { uid, photoURL } = auth.currentUser;

    await messagesRef.add({
      text: formValue,
      createdAt: firebase.firestore.FieldValue.serverTimestamp(),
      uid,
      photoURL,
    });

    setFormValue("");
    dummy.current.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <>
      <main>
        {messages &&
          messages.map((msg) => <ChatMessage key={msg.id} message={msg} />)}

        <span ref={dummy}></span>
      </main>

      <form onSubmit={sendMessage}>
        <input
          value={formValue}
          onChange={(e) => setFormValue(e.target.value)}
          placeholder="Type Something"
        />
        <button type="submit" disabled={!formValue} className="buttonenabled">
          <img
            src="https://image.shutterstock.com/image-vector/sending-message-icon-paper-plane-260nw-1727034373.jpg"
            alt="send"
            height="50"
            width="50"
          />
        </button>
      </form>
    </>
  );
}

function ChatMessage(props) {
  const { text, uid, photoURL } = props.message;
  const messageClass = uid === auth.currentUser.uid ? "sent" : "received";

  return (
    <>
      <div className={`message ${messageClass}`}>
        <Zoom>
          <img className="img" src={photoURL || ""} alt="Owner " />
          <p>{text}</p>
        </Zoom>
      </div>
    </>
  );
}

export default App;
